"use client";

import React, { useState, useEffect, memo } from 'react';
import { WifiOff, TrendingUp, TrendingDown, Minus, Activity, AlertTriangle, Database, RefreshCw } from 'lucide-react';
import { useLiveMarketData } from '../hooks/useLiveMarketData';
import { useModeGuard, useEffectiveSpot, useSnapshotAnalytics, useTimeoutProtection } from './SafeModeGuard';
import MarketStatusIndicator from './MarketStatusIndicator';
import DebugBadge from './DebugBadge';
import ProbabilityDisplay from './ProbabilityDisplay';
import ExpectedMoveChart from './ExpectedMoveChart';
import InstitutionalBias from './InstitutionalBias';
import SignalCards from './SignalCards';
import AIInterpretationPanel from './AIInterpretationPanel';

import MarketMetrics from './MarketMetrics';
import BiasMeter from './BiasMeter';
import OIHeatmap from './OIHeatmap';

import StructuralBannerFinal from './intelligence/StructuralBannerFinal';
import GammaPressurePanelFinal from './intelligence/GammaPressurePanelFinal';
import SmartMoneyPanel from './intelligence/SmartMoneyPanel';
import ExpiryPanelFinal from './intelligence/ExpiryPanelFinal';
import AlertPanelFinal from './intelligence/AlertPanelFinal';

// ‚îÄ‚îÄ Memoized ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
const MemoizedMarketMetrics = memo(MarketMetrics);
const MemoizedInstitutionalBias = memo(InstitutionalBias);
const MemoizedSmartMoney = memo(SmartMoneyPanel);
const MemoizedGammaPressure = memo(GammaPressurePanelFinal);
const MemoizedOIHeatmap = memo(OIHeatmap);
const MemoizedAIPanel = memo(AIInterpretationPanel);
const MemoizedAlerts = memo(AlertPanelFinal);
const MemoizedExpectedMove = memo(ExpectedMoveChart);
const MemoizedBiasMeter = memo(BiasMeter);
const MemoizedProbability = memo(ProbabilityDisplay);

// ‚îÄ‚îÄ Shared card style ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
const CARD = {
  background: 'rgba(255,255,255,0.03)',
  border: '1px solid rgba(255,255,255,0.07)',
  borderRadius: '14px',
  backdropFilter: 'blur(16px)',
} as const;

const CARD_HOVER_BORDER = 'rgba(0,229,255,0.18)';

// ‚îÄ‚îÄ Helper: section label ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
function SectionLabel({ children }: { children: React.ReactNode }) {
  return (
    <div
      className="text-[10px] font-bold tracking-[0.20em] uppercase mb-1"
      style={{ color: 'rgba(148,163,184,0.55)', fontFamily: "'JetBrains Mono', monospace" }}
    >
      {children}
    </div>
  );
}

// ‚îÄ‚îÄ Stat card ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
function StatCard({
  label, value, sub, accent = '#00E5FF',
}: { label: string; value: React.ReactNode; sub?: React.ReactNode; accent?: string }) {
  return (
    <div
      className="flex flex-col justify-between p-4 sm:p-5 rounded-2xl transition-all duration-300"
      style={CARD}
      onMouseEnter={(e) => (e.currentTarget.style.borderColor = CARD_HOVER_BORDER)}
      onMouseLeave={(e) => (e.currentTarget.style.borderColor = 'rgba(255,255,255,0.07)')}
    >
      <div
        className="text-[10px] font-semibold tracking-[0.18em] uppercase mb-2"
        style={{ color: 'rgba(148,163,184,0.55)', fontFamily: "'JetBrains Mono', monospace" }}
      >
        {label}
      </div>
      <div
        className="text-2xl sm:text-3xl font-black tabular-nums leading-none mb-1"
        style={{
          background: `linear-gradient(135deg, ${accent} 0%, #a78bfa 100%)`,
          WebkitBackgroundClip: 'text',
          WebkitTextFillColor: 'transparent',
          fontFamily: "'Space Grotesk', sans-serif",
        }}
      >
        {value}
      </div>
      {sub && (
        <div className="text-[11px] font-mono" style={{ color: 'rgba(148,163,184,0.5)' }}>
          {sub}
        </div>
      )}
    </div>
  );
}

// ‚îÄ‚îÄ Loading ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
function LoadingBlock() {
  return (
    <div className="flex flex-col items-center justify-center min-h-[60vh]">
      <div className="relative mb-6">
        <div
          className="w-16 h-16 rounded-full border-2 animate-spin"
          style={{ borderColor: 'rgba(0,229,255,0.15)', borderTopColor: '#00E5FF' }}
        />
        <div className="absolute inset-0 flex items-center justify-center">
          <div
            className="w-8 h-8 rounded-full border-2 animate-spin"
            style={{ borderColor: 'rgba(124,58,237,0.2)', borderTopColor: '#7C3AED', animationDirection: 'reverse' }}
          />
        </div>
      </div>
      <p
        className="text-sm font-mono animate-pulse tracking-widest uppercase"
        style={{ color: 'rgba(0,229,255,0.8)', fontFamily: "'JetBrains Mono', monospace" }}
      >
        Synchronizing‚Ä¶
      </p>
      <p className="text-xs mt-2" style={{ color: 'rgba(148,163,184,0.4)', fontFamily: "'JetBrains Mono', monospace" }}>
        Connecting to market feed
      </p>
    </div>
  );
}

function SnapshotReadyBlock() {
  return (
    <div
      className="flex flex-col items-center justify-center min-h-[40vh] mx-4 my-6 p-8 rounded-2xl"
      style={{ ...CARD, border: '1px solid rgba(59,130,246,0.20)' }}
    >
      <div
        className="w-14 h-14 rounded-2xl flex items-center justify-center mb-4"
        style={{ background: 'rgba(59,130,246,0.10)', border: '1px solid rgba(59,130,246,0.25)' }}
      >
        <Database className="w-7 h-7 text-blue-400" />
      </div>
      <h3 className="text-lg font-bold text-blue-300 tracking-wide mb-2">Snapshot Mode Active</h3>
      <p className="text-sm text-center max-w-md leading-relaxed mb-4" style={{ color: 'rgba(148,163,184,0.6)' }}>
        Using REST snapshot data ‚Äî market is currently closed
      </p>
      <div
        className="px-4 py-1.5 rounded-full text-xs font-mono font-bold"
        style={{
          background: 'rgba(59,130,246,0.10)',
          border: '1px solid rgba(59,130,246,0.22)',
          color: '#60a5fa',
        }}
      >
        MARKET CLOSED
      </div>
    </div>
  );
}

function ErrorBlock({ message }: { message: string }) {
  return (
    <div
      className="flex flex-col items-center justify-center min-h-[40vh] mx-4 my-6 p-8 rounded-2xl"
      style={{ ...CARD, border: '1px solid rgba(239,68,68,0.18)' }}
    >
      <div
        className="w-14 h-14 rounded-2xl flex items-center justify-center mb-4"
        style={{ background: 'rgba(239,68,68,0.08)', border: '1px solid rgba(239,68,68,0.22)' }}
      >
        <WifiOff className="w-7 h-7 text-red-400" />
      </div>
      <h3 className="text-lg font-bold text-red-300 tracking-wide mb-2">Connection Interrupted</h3>
      <p className="text-sm text-center max-w-md leading-relaxed mb-4" style={{ color: 'rgba(148,163,184,0.6)' }}>
        {message}
      </p>
      <div
        className="px-4 py-1.5 rounded-full text-xs font-mono font-bold"
        style={{
          background: 'rgba(239,68,68,0.08)',
          border: '1px solid rgba(239,68,68,0.22)',
          color: '#f87171',
        }}
      >
        OFFLINE
      </div>
    </div>
  );
}

// ‚îÄ‚îÄ Dashboard ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
interface DashboardProps { initialSymbol?: string; }

export default function Dashboard({ initialSymbol = "NIFTY" }: DashboardProps) {
  const [symbol, setSymbol] = useState(initialSymbol);
  const [expiryList, setExpiryList] = useState<string[]>([]);
  const [selectedExpiry, setSelectedExpiry] = useState<string | null>(null);

  const { data, status, error, loading, mode } = useLiveMarketData(symbol, selectedExpiry);

  const isLiveMode = useModeGuard(mode, 'LIVE');
  const isSnapshotMode = useModeGuard(mode, 'SNAPSHOT');
  const isHaltedMode = useModeGuard(mode, 'HALTED');
  const effectiveSpot = useEffectiveSpot(data, mode);
  const snapshotAnalytics = useSnapshotAnalytics(mode, (data as any)?.data_source || '');
  const timeoutProtection = useTimeoutProtection(mode);
  const isAnalyticsEnabled = (data as any)?.analytics_enabled !== false;

  React.useEffect(() => {
    if (mode !== 'live') {
      document.body.classList.add('snapshot-mode');
    } else {
      document.body.classList.remove('snapshot-mode');
    }
  }, [mode]);

  useEffect(() => {
    if (data?.available_expiries && data.available_expiries.length > 0) {
      setExpiryList(data.available_expiries);
      if (!selectedExpiry) setSelectedExpiry(data.available_expiries[0]);
    }
  }, [data?.available_expiries, selectedExpiry]);

  const safeError = typeof error === 'string' ? error : null;

  if (loading) {
    return mode === 'snapshot' ? <SnapshotReadyBlock /> : <LoadingBlock />;
  }
  if (safeError) return <ErrorBlock message={safeError} />;
  if (mode === 'snapshot' && !data) return <SnapshotReadyBlock />;

  // ‚îÄ‚îÄ Derived values ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
  const changePositive = (data?.change ?? 0) >= 0;
  const modeLabel = mode === 'live' ? 'LIVE' : mode === 'snapshot' ? 'SNAPSHOT' : mode === 'error' ? 'HALTED' : 'OFFLINE';
  const modeColor = mode === 'live' ? '#4ade80' : mode === 'snapshot' ? '#60a5fa' : '#f87171';

  return (
    <div
      className="min-h-screen text-white"
      style={{ background: 'radial-gradient(ellipse 100% 50% at 50% 0%, #0d1117 0%, #080b10 60%)' }}
    >
      {/* Subtle grid overlay */}
      <div
        className="pointer-events-none fixed inset-0 z-0"
        style={{
          backgroundImage: 'linear-gradient(rgba(0,229,255,1) 1px, transparent 1px), linear-gradient(90deg, rgba(0,229,255,1) 1px, transparent 1px)',
          backgroundSize: '48px 48px',
          opacity: 0.018,
        }}
      />

      <div className="relative z-10 max-w-[1920px] mx-auto px-3 sm:px-5 lg:px-8 py-4 sm:py-6 space-y-4">

        {/* ‚ïê‚ïê ROW 1 ‚Äî TICKER STRIP ‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê */}
        <div
          className="rounded-2xl px-4 sm:px-6 py-3 sm:py-4"
          style={{ ...CARD, border: '1px solid rgba(0,229,255,0.10)' }}
        >
          {/* Top accent */}
          <div
            className="h-[1px] w-full mb-3 rounded-full"
            style={{ background: 'linear-gradient(90deg, transparent, rgba(0,229,255,0.50), transparent)' }}
          />

          <div className="flex flex-wrap items-center gap-x-5 gap-y-2">
            {/* Symbol + live dot */}
            <div className="flex items-center gap-2">
              {mode === 'live' && (
                <span className="relative flex h-2 w-2">
                  <span className="animate-ping absolute inline-flex h-full w-full rounded-full bg-green-400 opacity-70" />
                  <span className="relative inline-flex rounded-full h-2 w-2 bg-green-400" />
                </span>
              )}
              <span
                className="text-lg sm:text-xl font-black tracking-widest"
                style={{
                  background: 'linear-gradient(90deg, #00E5FF, #818cf8)',
                  WebkitBackgroundClip: 'text',
                  WebkitTextFillColor: 'transparent',
                  fontFamily: "'Space Grotesk', sans-serif",
                }}
              >
                {symbol}
              </span>
            </div>

            {/* Spot price */}
            <div className="flex items-baseline gap-2">
              <span
                className="text-2xl sm:text-3xl font-black tabular-nums"
                style={{ color: '#fff', fontFamily: "'Space Grotesk', sans-serif" }}
              >
                {effectiveSpot?.toFixed(2) ?? '0.00'}
              </span>
              {mode === 'snapshot' && (
                <span
                  className="text-[10px] font-mono px-2 py-0.5 rounded-full"
                  style={{
                    color: '#60a5fa',
                    background: 'rgba(59,130,246,0.10)',
                    border: '1px solid rgba(59,130,246,0.22)',
                  }}
                >
                  REST
                </span>
              )}
            </div>

            {/* Change */}
            <div
              className="flex items-center gap-1 text-sm font-mono font-bold tabular-nums"
              style={{ color: changePositive ? '#4ade80' : '#f87171' }}
            >
              {changePositive ? <TrendingUp className="w-4 h-4" /> : <TrendingDown className="w-4 h-4" />}
              {changePositive ? '+' : ''}{data?.change_percent?.toFixed(2) ?? '0.00'}%
            </div>

            <div className="flex-1 hidden sm:block" />

            {/* Market status */}
            {status?.market_status && (
              <div
                className="flex items-center gap-1.5 px-3 py-1.5 rounded-full text-[11px] font-mono font-bold"
                style={
                  status.market_status === 'OPEN'
                    ? { background: 'rgba(34,197,94,0.10)', border: '1px solid rgba(34,197,94,0.28)', color: '#4ade80' }
                    : status.market_status === 'PRE_OPEN'
                      ? { background: 'rgba(234,179,8,0.10)', border: '1px solid rgba(234,179,8,0.28)', color: '#facc15' }
                      : { background: 'rgba(239,68,68,0.08)', border: '1px solid rgba(239,68,68,0.22)', color: '#f87171' }
                }
              >
                <span
                  className="w-1.5 h-1.5 rounded-full"
                  style={{
                    background: status.market_status === 'OPEN' ? '#4ade80' : status.market_status === 'PRE_OPEN' ? '#facc15' : '#f87171',
                    boxShadow: status.market_status === 'OPEN' ? '0 0 6px #4ade80' : 'none',
                  }}
                />
                {status.market_status}
              </div>
            )}

            {/* Engine mode */}
            <div
              className="flex items-center gap-1.5 px-3 py-1.5 rounded-full text-[11px] font-mono font-bold"
              style={{
                background: mode === 'live' ? 'rgba(34,197,94,0.08)' : mode === 'snapshot' ? 'rgba(59,130,246,0.08)' : 'rgba(255,255,255,0.04)',
                border: `1px solid ${modeColor}30`,
                color: modeColor,
              }}
            >
              <Activity className="w-3 h-3" />
              {modeLabel}
            </div>
          </div>
        </div>

        {/* ‚ïê‚ïê ROW 2 ‚Äî FOUR STAT CARDS ‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê */}
        <div className="grid grid-cols-2 lg:grid-cols-4 gap-3 sm:gap-4">
          <StatCard
            label="Expected Move"
            value={isAnalyticsEnabled ? (data?.intelligence?.probability?.expected_move || 0).toFixed(2) : 'N/A'}
            sub={isAnalyticsEnabled ? `¬±${(data?.intelligence?.probability?.upper_1sd || 0).toFixed(2)} SD` : 'Analysis disabled'}
            accent="#00E5FF"
          />
          <StatCard
            label="Put-Call Ratio"
            value={data?.analytics?.pcr?.toFixed(2) ?? '0.00'}
            sub={
              (data?.analytics?.pcr ?? 0) > 1 ? '‚ñ≤ Bullish Bias'
                : (data?.analytics?.pcr ?? 0) < 1 ? '‚ñº Bearish Bias'
                  : '‚Äî Neutral'
            }
            accent={
              (data?.analytics?.pcr ?? 1) > 1 ? '#4ade80'
                : (data?.analytics?.pcr ?? 1) < 1 ? '#f87171'
                  : '#94a3b8'
            }
          />
          <StatCard
            label="Total OI"
            value={((data?.analytics?.total_call_oi || 0) + (data?.analytics?.total_put_oi || 0)) > 1e6
              ? `${(((data?.analytics?.total_call_oi || 0) + (data?.analytics?.total_put_oi || 0)) / 1e6).toFixed(1)}M`
              : ((data?.analytics?.total_call_oi || 0) + (data?.analytics?.total_put_oi || 0)).toLocaleString()
            }
            sub={`Vol: ${(data?.analytics?.total_volume || 0).toLocaleString()}`}
            accent="#a78bfa"
          />
          <StatCard
            label="Breakout Risk"
            value={`${data?.intelligence?.probability?.breach_probability?.toFixed(0) ?? '0'}%`}
            sub={`VWAP: ${data?.analytics?.vwap?.toFixed(2) ?? '‚Äî'}`}
            accent="#fb923c"
          />
        </div>

        {/* ‚ïê‚ïê ROW 3 ‚Äî BIAS + EXPECTED MOVE BAR ‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê */}
        <div className="grid grid-cols-1 lg:grid-cols-12 gap-3 sm:gap-4">

          {/* Market Bias card */}
          <div
            className="lg:col-span-4 rounded-2xl p-4 sm:p-5 transition-all duration-300"
            style={CARD}
            onMouseEnter={(e) => (e.currentTarget.style.borderColor = CARD_HOVER_BORDER)}
            onMouseLeave={(e) => (e.currentTarget.style.borderColor = 'rgba(255,255,255,0.07)')}
          >
            <div className="flex items-center justify-between mb-4">
              <SectionLabel>Market Bias</SectionLabel>
              <span
                className="text-[11px] font-mono font-bold px-2.5 py-1 rounded-full"
                style={
                  data?.intelligence?.bias?.label === 'BULLISH'
                    ? { background: 'rgba(34,197,94,0.12)', border: '1px solid rgba(34,197,94,0.28)', color: '#4ade80' }
                    : data?.intelligence?.bias?.label === 'BEARISH'
                      ? { background: 'rgba(239,68,68,0.12)', border: '1px solid rgba(239,68,68,0.28)', color: '#f87171' }
                      : { background: 'rgba(148,163,184,0.08)', border: '1px solid rgba(148,163,184,0.18)', color: '#94a3b8' }
                }
              >
                {data?.intelligence?.bias?.label ?? 'HOLD'}
              </span>
            </div>

            {/* Strength bar */}
            <div className="mb-4">
              <div className="flex justify-between text-[10px] font-mono mb-1.5" style={{ color: 'rgba(148,163,184,0.55)' }}>
                <span>Strength Score</span>
                <span className="text-white">{data?.intelligence?.bias?.score?.toFixed(1) ?? '0.0'}</span>
              </div>
              <div className="w-full h-1.5 rounded-full overflow-hidden" style={{ background: 'rgba(255,255,255,0.06)' }}>
                <div
                  className="h-full rounded-full transition-all duration-700"
                  style={{
                    width: `${Math.abs(data?.intelligence?.bias?.score ?? 0) * 10}%`,
                    background: data?.intelligence?.bias?.label === 'BULLISH'
                      ? 'linear-gradient(90deg, #166534, #4ade80)'
                      : data?.intelligence?.bias?.label === 'BEARISH'
                        ? 'linear-gradient(90deg, #991b1b, #f87171)'
                        : 'linear-gradient(90deg, #334155, #94a3b8)',
                  }}
                />
              </div>
            </div>

            <div className="grid grid-cols-2 gap-2">
              {[
                { label: 'Confidence', value: Math.abs(data?.intelligence?.bias?.score ?? 0) > 0.7 ? 'HIGH' : Math.abs(data?.intelligence?.bias?.score ?? 0) > 0.4 ? 'MED' : 'LOW' },
                { label: 'Pressure', value: data?.intelligence?.bias?.label === 'BULLISH' ? 'BUYING' : data?.intelligence?.bias?.label === 'BEARISH' ? 'SELLING' : 'BALANCED' },
              ].map(({ label, value }) => (
                <div
                  key={label}
                  className="rounded-xl px-3 py-2.5"
                  style={{ background: 'rgba(255,255,255,0.03)', border: '1px solid rgba(255,255,255,0.06)' }}
                >
                  <div className="text-[10px] font-mono mb-0.5" style={{ color: 'rgba(148,163,184,0.5)' }}>{label}</div>
                  <div className="text-[12px] font-mono font-bold text-white">{value}</div>
                </div>
              ))}
            </div>
          </div>

          {/* Expected Move Range Bar */}
          <div
            className="lg:col-span-8 rounded-2xl p-4 sm:p-5 transition-all duration-300"
            style={CARD}
            onMouseEnter={(e) => (e.currentTarget.style.borderColor = CARD_HOVER_BORDER)}
            onMouseLeave={(e) => (e.currentTarget.style.borderColor = 'rgba(255,255,255,0.07)')}
          >
            <div className="flex items-center justify-between mb-4">
              <SectionLabel>Expected Move Range</SectionLabel>
              {isSnapshotMode && (
                <span
                  className="text-[10px] font-mono px-2 py-0.5 rounded-full"
                  style={{ color: '#60a5fa', background: 'rgba(59,130,246,0.08)', border: '1px solid rgba(59,130,246,0.20)' }}
                >
                  REST Premiums
                </span>
              )}
            </div>

            <div className="flex items-center gap-3 mb-3">
              <span className="text-sm font-mono font-bold tabular-nums w-24 text-right shrink-0 text-red-400">
                {(data?.spot - (data?.intelligence?.probability?.expected_move ?? 0)).toFixed(2)}
              </span>
              <div
                className="flex-1 relative h-5 rounded-full overflow-hidden"
                style={{ background: 'rgba(0,0,0,0.30)', border: '1px solid rgba(255,255,255,0.07)' }}
              >
                <div className="absolute inset-y-0 left-0 right-0 rounded-full" style={{ background: 'linear-gradient(90deg, rgba(239,68,68,0.25), rgba(99,102,241,0.15) 50%, rgba(34,197,94,0.25))' }} />
                <div className="absolute inset-y-0 left-1/2 -translate-x-1/2 w-px" style={{ background: 'rgba(255,255,255,0.30)' }} />
                <div className="absolute inset-y-0 left-1/2 -translate-x-1/2 flex items-center">
                  <div className="w-3.5 h-3.5 rounded-full bg-white border-2" style={{ borderColor: '#080b10', boxShadow: '0 0 10px rgba(255,255,255,0.7)' }} />
                </div>
              </div>
              <span className="text-sm font-mono font-bold tabular-nums w-24 shrink-0 text-green-400">
                {(data?.spot + (data?.intelligence?.probability?.expected_move ?? 0)).toFixed(2)}
              </span>
            </div>

            <div className="flex justify-between text-[10px] font-mono px-1 mb-4" style={{ color: 'rgba(148,163,184,0.50)' }}>
              <span>‚Üì Lower bound</span>
              <span className="text-white">Spot: {data?.spot?.toFixed(2) ?? '‚Äî'}</span>
              <span>Upper bound ‚Üë</span>
            </div>

            <div
              className="inline-flex items-center gap-2 text-[11px] font-mono px-3 py-1.5 rounded-xl"
              style={{
                background: 'rgba(251,146,60,0.08)',
                border: '1px solid rgba(251,146,60,0.22)',
                color: '#fb923c',
              }}
            >
              <AlertTriangle className="w-3.5 h-3.5" />
              BREAKOUT RISK: {data?.intelligence?.probability?.breach_probability?.toFixed(0) ?? '0'}%
            </div>
          </div>
        </div>

        {/* ‚ïê‚ïê ROW 4 ‚Äî SMART MONEY + LIQUIDITY ‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê */}
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-3 sm:gap-4">

          {/* Smart Money */}
          <div
            className="rounded-2xl p-4 sm:p-5 transition-all duration-300"
            style={CARD}
            onMouseEnter={(e) => (e.currentTarget.style.borderColor = CARD_HOVER_BORDER)}
            onMouseLeave={(e) => (e.currentTarget.style.borderColor = 'rgba(255,255,255,0.07)')}
          >
            <div className="flex items-center justify-between mb-3">
              <SectionLabel>Smart Money</SectionLabel>
              {isSnapshotMode && (
                <span
                  className="text-[10px] font-mono px-2 py-0.5 rounded-full"
                  style={{ color: '#60a5fa', background: 'rgba(59,130,246,0.08)', border: '1px solid rgba(59,130,246,0.20)' }}
                >
                  DISABLED
                </span>
              )}
            </div>
            {isLiveMode ? (
              <MemoizedSmartMoney smartMoneyData={data?.smart_money_activity ?? null} />
            ) : (
              <div className="flex flex-col items-center justify-center h-16 gap-2">
                <Minus className="w-5 h-5" style={{ color: 'rgba(148,163,184,0.3)' }} />
                <span className="text-[11px] text-center font-mono" style={{ color: 'rgba(148,163,184,0.5)' }}>
                  Disabled in {mode.toUpperCase()} mode
                </span>
              </div>
            )}
          </div>

          {/* Liquidity */}
          <div
            className="rounded-2xl p-4 sm:p-5 transition-all duration-300"
            style={CARD}
            onMouseEnter={(e) => (e.currentTarget.style.borderColor = CARD_HOVER_BORDER)}
            onMouseLeave={(e) => (e.currentTarget.style.borderColor = 'rgba(255,255,255,0.07)')}
          >
            <SectionLabel>Liquidity</SectionLabel>
            <div className="mt-3 space-y-2.5">
              {[
                { label: 'Total OI', value: ((data?.analytics?.total_call_oi || 0) + (data?.analytics?.total_put_oi || 0)).toLocaleString() },
                { label: 'OI Change', value: data?.analytics?.oi_change_24h?.toFixed(0) ?? '0' },
                { label: 'Volume', value: (data?.analytics?.total_volume || 0).toLocaleString() },
                { label: 'VWAP', value: data?.analytics?.vwap?.toFixed(2) ?? '0.00' },
              ].map(({ label, value }) => (
                <div key={label} className="flex justify-between items-center">
                  <span className="text-[11px] font-mono" style={{ color: 'rgba(148,163,184,0.55)' }}>{label}</span>
                  <span className="text-[11px] font-mono font-semibold text-white tabular-nums">{value}</span>
                </div>
              ))}
            </div>
          </div>
        </div>

        {/* ‚ïê‚ïê SNAPSHOT BANNER ‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê */}
        {isSnapshotMode && (
          <div
            className="rounded-2xl px-5 py-4 flex items-center gap-4"
            style={{ background: 'rgba(59,130,246,0.06)', border: '1px solid rgba(59,130,246,0.18)' }}
          >
            <div
              className="shrink-0 w-9 h-9 rounded-xl flex items-center justify-center"
              style={{ background: 'rgba(59,130,246,0.12)', border: '1px solid rgba(59,130,246,0.22)' }}
            >
              <Database className="w-4 h-4 text-blue-400" />
            </div>
            <div>
              <div className="text-xs font-mono font-bold text-blue-400 tracking-wide">SNAPSHOT MODE ‚Äî Market Closed</div>
              <div className="text-[10px] font-mono mt-0.5" style={{ color: 'rgba(96,165,250,0.55)' }}>
                Displaying last available EOD snapshot. Live data resumes at market open.
              </div>
            </div>
          </div>
        )}

        {/* ‚ïê‚ïê EXPIRY SELECTOR ‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê */}
        {expiryList.length > 0 && (
          <div
            className="rounded-2xl p-4 sm:p-5 flex flex-col sm:flex-row sm:items-center gap-3"
            style={CARD}
          >
            <label
              className="text-[10px] font-mono font-bold tracking-widest uppercase whitespace-nowrap shrink-0"
              style={{ color: 'rgba(148,163,184,0.55)' }}
            >
              Expiry Date
            </label>
            <select
              value={selectedExpiry || ''}
              onChange={(e) => setSelectedExpiry(e.target.value)}
              className="flex-1 text-white text-sm font-mono px-3 py-2 rounded-xl outline-none transition-all cursor-pointer"
              style={{
                background: 'rgba(0,0,0,0.35)',
                border: '1px solid rgba(0,229,255,0.15)',
                color: '#e2e8f0',
              }}
            >
              {expiryList.map((exp) => (
                <option key={exp} value={exp}>{exp}</option>
              ))}
            </select>
          </div>
        )}

        {/* ‚ïê‚ïê OPTION CHAIN PANEL ‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê */}
        <div
          className="rounded-2xl overflow-hidden"
          style={CARD}
        >
          <div className="h-[1px] w-full" style={{ background: 'linear-gradient(90deg, transparent, rgba(99,102,241,0.5), transparent)' }} />
          <div className="p-4 sm:p-5">
            <div className="flex items-center justify-between mb-3">
              <SectionLabel>Option Chain</SectionLabel>
              {data?.optionChain && (
                <span className="text-[10px] font-mono" style={{ color: 'rgba(148,163,184,0.45)' }}>
                  {data.optionChain.calls?.length ?? 0}CE ¬∑ {data.optionChain.puts?.length ?? 0}PE
                </span>
              )}
            </div>
            {data?.optionChain ? (
              <div className="text-xs font-mono" style={{ color: 'rgba(148,163,184,0.6)' }}>
                Option Chain Data Available ‚Äî {data.optionChain.calls?.length ?? 0} calls, {data.optionChain.puts?.length ?? 0} puts
              </div>
            ) : (
              <div className="flex items-center justify-center py-6" style={{ color: 'rgba(148,163,184,0.3)' }}>
                <Activity className="w-5 h-5 mr-2" />
                <span className="text-xs font-mono">Awaiting data‚Ä¶</span>
              </div>
            )}
          </div>
        </div>

        {/* ‚ïê‚ïê OI HEATMAP ‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê */}
        <div
          className="rounded-2xl overflow-hidden"
          style={CARD}
        >
          <div className="h-[1px] w-full" style={{ background: 'linear-gradient(90deg, transparent, rgba(245,158,11,0.40), transparent)' }} />
          <div className="p-4 sm:p-5">
            <MemoizedOIHeatmap symbol={symbol} liveData={data?.optionChain ?? null} />
          </div>
        </div>

        {/* ‚ïê‚ïê AI PANEL + ALERTS + DEBUG ‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê */}
        <DebugBadge className="mb-1" />
        <MemoizedAIPanel intelligence={data?.intelligence ?? null} />
        <MemoizedAlerts alerts={data?.alerts || []} />

      </div>
    </div>
  );
}
